
























































































































































































































































































































































@import '../styles/variables.scss';

$identation1: 40px;
$identation2: #{$identation1 * 2};
$identation3: #{$identation1 * 3};

$yellow: #ffc600;
$orange: #ff7200;
$red: #f44542;
$purple: #fb94ff;
$green: #a5ff90;
$lightblue: #9effff;

.aboutMe {
    width: 100%;
    display: flex;
    position: relative;
    padding: calc(#{$navHeight + 40}) 0 calc(#{$footerHeight + 40});

    .animatedLetters {
        @include fontXXL;
        font-weight: $fontBold;
        text-align: left;
        margin-bottom: 40px;
    }

    code {
        display: block;
        position: relative;
        @include fontM;
        margin-left: 60px;

        &:before {
            content: '';
            width: 5px;
            height: 100%;
            position: absolute;
            top: 0;
            left: -20px;
            background: $white;
        }

        p {
            min-height: 26px;
            position: relative;

            &.identation1 {
                padding-left: $identation1;
            }

            &.identation2 {
                padding-left: $identation2;
            }

            &.identation3 {
                padding-left: $identation3;
            }

            &:before {
                content: attr(linenumber);
                position: absolute;
                top: 0;
                left: -50px;
                color: #aaa;
            }

            .yellow {
                color: $yellow;
            }

            .orange {
                color: $orange;
            }

            .red {
                color: $red;
            }

            .purple {
                color: $purple;
            }

            .green {
                color: $green;
            }

            .lightblue {
                color: $lightblue;
            }

        }

    }

    @include tablet {
        code {
            margin-left: 30px;

            &:before {
                left: -14px;
            }

            p:before {
                left: -40px;
            }

        }
    }

}
